$grey_color: #222222;
$blue_color: #1eacc7;
$red_color: #ff5656;


* {
	font-family: 'Roboto', sans-serif; }

header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 2; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;

	.menu_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300; } }

.menu_link:hover {
	color: #ffffff; }

.menu .menu_item {
	border-right: 2px dotted black;
	padding-right: 28px; }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background: rgba(0, 0, 0, 0.6);

	.subheader_logo {
		display: block;
		margin-top: 16px;
		max-width: 100%; }

	.subheader_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }

	.subheader_call {
		margin-top: 19px;
		text-align: right;
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		line-height: 14px; }

	.subheader_phone {
		margin-top: 8px;
		text-align: right;
		color: #fff;
		font-size: 22px;
		font-weight: 400; }

	.subheader_btn {
		margin-top: 19px;
		text-transform: uppercase;
		color: #fff;
		border: none;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border-radius: 5px;
		font-size: 14px;
		font-weight: 400;
		background-image: linear-gradient(to top, $blue_color 0%, #4ce2ff 100%); } }

.promo {
	min-height: 800px;
	background: url('../img/Backgrounds/Car_bg.jpg') center (center / cover) no-repeat;

	.promo_header {
		color: #fff;
		font-size: 60px;
		text-align: center;
		font-weight: 700;
		line-height: 60px;
		margin-top: 150px;
		margin-bottom: 0px; }

	.promo_subheader {
		color: #fff;
		font-size: 36px;
		text-align: center;
		font-weight: 700;
		line-height: 48px;
		margin-bottom: 0px; }

	.promo_text {
		color: #fff;
		font-size: 18px;
		text-align: center;
		font-weight: 400;
		line-height: 24px;
		margin-top: 23px; }

	.promo_btn {
		display: block;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 5px;
		font-size: 18px;
		font-weight: 400;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		margin: 118px auto 0 auto;
		color: #fff; } }

.reasons {
	padding: 71px 0 65px 0; }

.label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center; }

.title {
	margin-bottom: 0;
	margin-top: 38px;
	color: $grey_color;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }

.subtitle {
	color: $grey_color;
	font-size: 17px;
	font-weight: 400;
	margin-top: 19px;
	text-align: center; }

.reasons .reasons_block {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 35px;

	.reasons_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $blue_color;
		border-radius: 100%; }

	.reasons_descr {
		width: 343px;

		.reasons_subtitle {
			line-height: 21px;
			color: $blue_color;
			font-size: 17px;
			font-weight: 400; }

		.reasons_text {
			color: $grey_color;
			font-size: 15px;
			font-weight: 300;
			line-height: 21px; } } }

.mobile {
	min-height: 864px;
	background: url('../img/Backgrounds/2_screen_bg.jpg') center (center / cover) no-repeat;
	padding: 48px 0 79px 0;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/screen_2_images/1_image.jpg') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/screen_2_images/2_image.jpg') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/screen_2_images/3_image.jpg') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/screen_2_images/4_image.jpg') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/screen_2_images/5_image.jpg') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/screen_2_images/6_image.jpg') center (center / cover) no-repeat; }
		&_subtitle {
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase;
			color: #ffffff;
			text-align: center;
			line-height: 32px; }
		&_plus {
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			margin: 0 auto;
			cursor: pointer; } } }

.car {
	min-height: 905px;
	background: url('../img/Backgrounds/4_screen.jpg') center (center / cover) no-repeat;
	padding: 56px 0 130px 0;
	.label {
		background-color: #ffffff;
		color: #000000; }
	.title {
		color: #ffffff; }
	.subtitle {
		color: #ffffff; }
	&_image {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 36px; }
	&_descr {
		color: $grey_color;
		font-size: 17px;
		font-weight: 200;
		text-align: center; } }
